import { React, useState } from "react";
import bootstrap from "bootstrap/dist/css/bootstrap.css";


const Principal = () => {
  // const [opcioncompu,setOpcionCompu] = useState(null);
  const [estado, setEstado] = useState("");
  const [msg, setMsg] = useState("");

  async function jugar(opc) {
    let opciones = ["Piedra", "Papel", "Tijeras"];
    let random = await Math.floor(Math.random() * opciones.length);
    let op_compu = opciones[random];

    if (
      (opc === "Piedra" && op_compu === "Tijeras") ||
      (opc === "Papel" && op_compu === "Piedra") ||
      (opc === "Tijeras" && op_compu === "Papel")
    ) {
      setEstado("Has Ganado!");
      setMsg("Usuario: " + opc + " Computadora: " + op_compu);
    } else if (opc === op_compu) {
      setEstado("Empate");
      setMsg("Usuario: " + opc + " Computadora: " + op_compu);
    } else {
      setEstado("Has perdido");
      setMsg("Usuario: " + opc + " Computadora: " + op_compu);
    }
  }

  return (
    <div>
      <div className="container mt-5">
        <h1 className="text-center">Piedra papel tijera</h1>
        <h1 className="text-center mt-5">
          {estado == "Has Ganado!" ? (
            <h1 style={{ color: "Green" }}>Has Ganado!</h1>
          ) : estado == "Empate" ? (
            <h1 style={{ color: "black" }}>Empate</h1>
          ) : (
            <h1 style={{ color: "red" }}>Has Perdido :(</h1>
          )}
        </h1>
        <h4 className="text-center mt-3">{msg}</h4>


        <div className="botones text-center mt-5">
          <h4>Elige una opcion</h4>
          <button
            className="btn btn-primary"
            value="Piedra"
            onClick={(e) => {
              jugar(e.target.value);
            }}
            style={{"margin-top":"3rem","width":"7rem"}}

          >
            Piedra
          </button>
          <button
            className="btn btn-primary ml-3"
            value="Papel"
            onClick={(e) => {
              jugar(e.target.value);
            }}
            style={{"margin-left":"3rem","margin-top":"3rem","width":"7rem"}}
          >
            Papel
          </button>
          <button
            className="btn btn-primary ml-3"
            value="Tijeras"
            onClick={(e) => {
              jugar(e.target.value);
            }}
            style={{"margin-left":"3rem","margin-top":"3rem","width":"7rem"}}


          >
            Tijera
          </button>
        </div>
      </div>
    </div>
  );
};

export default Principal;
